@use "../base/variables" as *;

.card-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  gap: 32px;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0;

  @media (max-width: $tablet) {
    max-width: 800px;
    margin: 0 auto;
    gap: 37px 28px;
  }

  @media (max-width: $mobile) {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    row-gap: 32px;
  }
}

.card-list__item {
  position: relative;
  width: 372px;
  min-height: 372px;

  @media (max-width: $tablet) {
    width: 336px;
    min-height: 420px;
  }

  @media (max-width: $mobile) {
    display: flex;
    justify-content: stretch;
    align-items: flex-end;
    width: 288px;
    min-height: 347px;
    border-radius: $border-radius-big;
  }
}
